@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
nav {
    background-color: rgb(24, 110, 160);
    height: 6em;

}

nav ul {
    display: flex;
    justify-content: flex-end;

    align-items: center;
}

.nav-box {
    display: flex;
    justify-content: space-between;
}

nav li {
    list-style: none;
    margin: 1.5em;
    color: white;
    background-color: rgba(240, 255, 255, 0.36);
    padding: 10px;
    border-radius: 5px;

}

nav a {
    text-decoration: none;
    color: white;
    font-size: 1.3em;
}

.search {
    align-self: flex-start;
    justify-self: flex-start;
}

@media only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px) {


    nav {
        height: unset;


    }

    nav ul {
        justify-content: center;
    }

    nav a {
        font-size: 1.2em;
    }

    nav li {
        margin: 1.1em;
        font-size: 0.9em;
    }


}

@media only screen and (max-width: 400px) {
    nav li {
        font-size: 0.8em;
    }
}
.search-btn {
    background-color: transparent;
    border: none;
    color: white;
    text-decoration: none;
    font-size: 1.3em;


}

.search-box {
    display: flex;
    margin: 0 20px;


}

.search {

    align-self: center;

    margin: auto;

}

.search>div {
    display: inline-block;
    position: relative;
    filter: drop-shadow(0 1px #0091c2);
    font-family: 'Roboto', 'sans-serif';
}

.search>div:after {
    content: "";
    background: white;
    width: 4px;
    height: 20px;
    position: absolute;
    top: 25px;
    right: -4px;
    transform: rotate(135deg);
}

.search>div>input {
    color: white;
    font-size: 16px;
    background: transparent;
    width: 35px;
    height: 35px;
    padding: 10px;
    border: solid 3px white;
    outline: none;
    border-radius: 35px;
    transition: width 0.5s;
}

.search>div>input::placeholder {
    color: #efefef;
    opacity: 0;
    transition: opacity 150ms ease-out;
}

.search>div>input:focus::placeholder {
    opacity: 1;
}

.search>div>input:focus,
.search>div>input:not(:placeholder-shown) {
    width: 250px;
}
.background {
    background-color: #44c4e791;
    max-width: 1800px;
    position: relative;
    min-height: 100vh;

}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 25px auto;
    max-width: 500px;
    background-color: white;
    padding: 15px;







}


.container label,
input {
    display: block;
}

form input {
    outline: none;
    margin: 0 0 20px;
    padding: 10px 15px;

    width: 300px;
    border: 1px solid #ccc;
    color: #767575;
    font-family: "Roboto";
    box-sizing: border-box;
    font-size: 14px;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transition: 0.2s linear;



}

form button {
    cursor: pointer;
    background: #44c4e7;
    width: 100%;
    padding: 10px 15px;
    border: 0;
    color: #fff;
    font-family: "Roboto";
    font-size: 14px;
    font-weight: 400;
}

form button:hover {
    background: #369cb8;
}

@media screen and (max-width: 500px) {
    .container {
        width: 100%;
        max-width: 350px;
    }

    form input {
        width: 100%;
        max-width: 350px;
    }
}

@media screen and (max-width: 380px) {
    .container {
        width: 100%;
        max-width: 250px;
    }

    form input {
        width: 100%;
        max-width: 250px;
    }
}
.patient-container {
    display: flex;
    flex-direction: column;
    border-top: 1px solid black;
    align-items: center;
    min-height: 100vh;
}

.patient-container h2 {
    margin-top: 1em;
}

.list-items {
    display: flex;
    max-width: 1150px;
    padding: 15px;
    border: 1px solid #ddd;
    margin-top: 1em;

}

.card li {
    border: 1px red solid;
    margin: 10px;
    list-style: none;
}

table {
    width: 100%;
    border-collapse: collapse;



}

tr,
td,
th {
    padding: 15px;
    margin: 15px;
    text-align: center;
    background-color: white;


}

tbody tr:hover {
    background-color: rgba(31, 142, 206, 0.77);

}

tbody tr:last-child,
table tr:last-child {
    border-bottom: none;
}

table tr {
    border-bottom: 1px solid #ddd;
}

table th {
    border-bottom: 1px solid #ddd;
}


table button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 5px;
    text-align: center;
    margin: 0 auto;

}

.icon {

    font-size: 1.6em;
}

.error {
    text-align: center;
    font-weight: 600;
    margin-top: 1rem;

}

.highlighted {
    color: red;
}



@media only screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px) {

    .list-items {
        display: block;
        padding: 0;
        margin-top: 0;
        border: none;


    }

    .patient-container {
        display: block;
    }

    .patient-container h2 {
        text-align: center;
    }

    /* Force table to not be like tables anymore */
    table,
    thead,
    tbody,
    th,
    td,
    tr {
        display: block;



    }



    /* Hide table headers (but not display: none;, for accessibility) */
    thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;

    }

    tr {
        border: 1px solid #ccc;
    }

    td {
        /* Behave  like a "row" */
        border: none;
        border-bottom: 1px solid #eee;
        position: relative;
        padding-left: 50%;

    }

    td:before {
        /* Now like a table header */
        position: absolute;
        /* Top/left values mimic padding */
        top: 10px;
        left: 6px;
        width: 45%;
        padding-right: 10px;
        white-space: nowrap;
        font-weight: 700;

    }

    /*
	Label the data
	*/
    td:nth-of-type(1):before {
        content: "Dátum a čas";

    }

    td:nth-of-type(2):before {
        content: "Meno";
    }

    td:nth-of-type(3):before {
        content: "Priezvisko";
    }

    td:nth-of-type(4):before {
        content: "Rodné číslo";
    }

    td:nth-of-type(5):before {
        content: "Liek";
    }

    td:nth-of-type(6):before {
        content: "Dávka";
    }

    td:nth-of-type(7):before {
        content: "Šarža";
    }

    td:nth-of-type(8):before {
        content: "Počet";
    }

    td:nth-of-type(9):before {
        content: "Vymazať";
    }

}
.form-container h2 {
    text-align: center;
}

.form-container button {
    margin: 1em auto;
}
.login-form {
    margin: 0 auto;
    width: 300px;


}

.login-container h2 {

    text-align: center;
    margin: 1em;
    padding: 1em;
}

.login-form p {
    margin-top: 1em;
}

.login-details {
    display: flex;
    flex-direction: column;
}

.login-details p:first-child {
    text-align: center;
}
